    /**
     * 
     *
     * @param {json object} paramObj 
     * @param {string} platform UI
     * @param {boolean} FB Facebooktrue
     * @param {boolean} enableCache truefalseUI
     * @param {function} success, json
     * @param {function} fail, 
     * @param {function} cancel, 
     * @return {json object} 
     * @return {number} code 
     * @return {string} msg 
     * @return {string} platform 
     * @return {number} userId Id
     * @return {string} token token
     * @return {string} puserId Id
     */
    wingplus.user.login = function(paramObj){
        //Added by Zping on 20180518 begin
        //platformH5:GUEST, :WINGA
        if(paramObj && paramObj.platform === 'GUEST' && util.BroswerUtil.isMiniClientExcludeCG()){
            paramObj.platform = 'WINGA';
        }
        //End

        // wing.user.login(paramObj);
        tmpLoginSuccessFun = paramObj.success;
        paramObj.success = function(result){
            console.log("--");
            console.log(JSON.stringify(result));

            if(result.context){
               define.context=result.context;
               define.context.userLogin=true
               result.context=null;
               if(result.hasOwnProperty('userFlag')){
                    //
                    util.store.write("wing_is_bind_user",result.isBindUser,1);//
                    util.store.write("wing_user_flag",result.userFlag,1);//userFlag=0userFlag=1
                    util.store.write("wing_user_token",result.token,1)
                    util.store.write("wing_user_id",result.userId,1)
                    if(!util.store.read("wing_ck_dig_hide") && !reCkLogin && !util.store.read("wing_user_switch")){
                        tmpLoginResult = result;
                        bind.selectAccountModal({
                            success:function(data){
                                console.log(data);
                                util.store.remove("wing_user_switch");
                                if(data.value==tmpLoginResult.userFlag){//
									util.store.write("wing_user_token",tmpLoginResult.token,1);
									util.store.write("wing_user_id",tmpLoginResult.userId,1);
                                    callbackFunc(tmpLoginSuccessFun,tmpLoginResult);
                                }else {//
                                    util.store.write("wing_user_flag",data.value,1);
                                    if (util.BroswerUtil.isMiniClientExcludeCG()){
                                    	 paramObj.platform=result.platform;
                                    	 paramObj.userFlag=data.value;
                                    }
                                    
                                    reCkLogin = true;
                                    paramObj.success = tmpLoginSuccessFun;
                                    wingplus.user.login(paramObj);
                                }
                            }
                        })

                        return;
                    }
                    reCkLogin =false;
                }else{
                	util.store.remove("wing_is_bind_user");
                    util.store.remove("wing_user_flag");
                    util.store.remove("wing_ck_dig_hide");
                    util.store.remove("wing_user_switch");
                }

            }
            util.store.write("wing_user_token",result.token,1)
            util.store.write("wing_user_id",result.userId,1)
            util.store.remove("wing_user_switch");
            callbackFunc(tmpLoginSuccessFun,result);
        }
        
        if (util.BroswerUtil.isMiniClientExcludeCG() && util.store.read("wing_user_flag")){
        	 paramObj.userFlag=util.store.read("wing_user_flag");
        }
        checkInitAndInvokeFunc("wing.user.login", paramObj);
    }

    define.context ={}
    var tmpLoginSuccessFun,tmpLoginResult,reCkLogin=false;

