    /**
     * 
     * callback
     */
    wingplus.pay.isPayServiceAvailable = function(callback){
        if(util.isNull(callback)){
            console.warn("isPayServiceAvailable callback should not be null");
            return;
        }
        wing.pay.isPayServiceAvailable(callback);
    }

    /**
     * 
     *
     * @param {json object} paramObj 
     * @param {function} success, json
     * @param {function} fail, 
     * @param {function} cancel, 
     * @return {json object} 
     * @return {number} code 
     * @return {string} msg 
     * @return {json object} productList 
     * @return {string} productId ID,
     * @return {number} productName 
     * @return {string} productDesc 
     * @return {number} gameCurrencyAmount iOS
     */
    wingplus.pay.getProducts = function(paramObj){

        wing.pay.isPayServiceAvailable(function(isPayAvailable){
            if(isPayAvailable){
                if(util.BroswerUtil.isMiniClientExcludeCG()){
                    wing.pay.queryProducts(paramObj);
                }else{
                    var products=  wing.pay.getProducts();
                    var productsResult = {};
        
                    if(products){
                        //
                        if(products.length > 0){
                            productsResult.code = 200;
                            productsResult.msg = "Get products successfully!";
                            productsResult.productList = products;
                            callbackFunc(paramObj.success, productsResult);
                        }else{
                            //
                            productsResult.code = 400;
                            productsResult.msg = "Get products fail!";
                            callbackFunc(paramObj.fail, productsResult);
                        }
                    }else{
                        //
                        productsResult.code = -100;//
                        productsResult.msg = "Get products cancel!";
                        callbackFunc(paramObj.cancel, productsResult);
                    }
                }
            }else{
                var productsResult = {};
                //
                productsResult.code = 4026;
                productsResult.msg = "Payment is closed";
                callbackFunc(paramObj.fail, productsResult);
            }
        })
    }

    /**
     * 
     *
     * @param {json object} paramObj 
     * @param {string} channel, h5sdkFACEBOOK
     * @param {string} productId, Id
     * @param {function} success, 
     * @param {function} fail, 
     * @param {function} cancel, 
     * @return {json object} 
     * @return {number} code 
     * @return {string} msg 
     * @return {string} amount ID,
     * @return {string} currency 
     * @return {number} quantity 1
     * @return {string} platform 
     * @return {string} orderId Id
     * @return {string} productId Id
     * @return {string} payment_id h5FACEBOOK
     * @return {string} request_id h5FACEBOOK
     * @return {string} signed_request h5FACEBOOK
     * @return {string} status h5FACEBOOK
     */
    wingplus.pay.pay = function(paramObj){
        wing.pay.isPayServiceAvailable(function(isPayAvailable){
            if(isPayAvailable){
                if(util.BroswerUtil.isMiniClientExcludeCG()){
                    wing.pay.pay(paramObj);
                }else{
                    wing.pay.payUI(paramObj);
                }
            }else{
                var productsResult = {};
                //
                productsResult.code = 4026;
                productsResult.msg = "Payment is closed";
                callbackFunc(paramObj.fail, productsResult);
            }
        })
    }
